.social__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  padding: 0;
  margin: 0;
  margin-left: 10px;

  @media screen and (max-width: 767px) {
    justify-content: center;
  }
}

.social__item {
  width: 40px;
  height: 40px;
  padding: 5px;
  margin-right: 15px;
  margin-bottom: 10px;

  @media screen and (max-width: 1023px) {
    margin-right: 0;
    margin-left: 15px;
  }

  @media screen and (max-width: 767px) {
    margin-right: 15px;
    margin-left: 0;
  }
}

.social__list:nth-child(3n) {
  margin-right: 0;
}

.social__link {
  list-style-type: none;
  color: $color-alice-blue;
}




.social__link:hover {
  color: $color-deep-sky-blue;
}
