.form__fieldset {
  display: flex;
  width: 585px;
  border: none;
  padding: 0;
  flex-wrap: wrap;

  @media screen and (max-width: 767px) {
    flex-direction: column;
    width: 270px;
  }
}

.form__fieldset--modal {
  width: 420px;
  box-sizing: border-box;
  flex-direction: column;
}

.form__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 51px;
  margin-bottom: 15px;

  @media screen and (max-width: 1023px) {
    margin-bottom: 5px;
  }

  @media screen and (max-width: 767px) {
    font-size: 30px;
    line-height: 42px;
    margin-bottom: 8px;
  }
}

.form__text {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  margin-bottom: 33px;

  @media screen and (max-width: 1023px) {
    margin: 0;
    margin-bottom: 25px;

  }

  @media screen and (max-width: 767px) {
    font-size: 14px;
    line-height: 19px;
    margin: 0;
    margin-bottom: 35px;
  }
}

.form__text--modal {
  font-size: 14px;
  line-height: 16px;
}

.form__input {
  box-sizing: border-box;
  width: 275px;
  height: 50px;
  margin-bottom: 20px;
  border: none;
  border-radius: 3px;
  padding: 13px;
  background-color: $color-transparent;
  cursor: pointer;

  @media screen and (max-width: 1023px) {
    width: 320px;
  }

  @media screen and (max-width: 767px) {
    width: 270px;
    margin-bottom: 20px;
  }
}

.form__input:hover {
  background-color: $color-default-white;
}

.form__input--phone {
  padding: 0;
  background-color: transparent;
}

.form__input--modal {
  min-width: 420px;
}

.form__input--modal:focus {
  background-color: $color-default-white;
}

.form__first-row {
  width: 580px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media screen and (max-width: 1023px) {
    width: 670px;
  }

  @media screen and (max-width: 767px) {
    flex-direction: column;
    width: 270px;
  }
}

.form__input::placeholder {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: $color-default-white;
}

.form__textarea {
  width: 580px;
  min-height: 155px;
  background-color: $color-transparent;
  border: none;
  border-radius: 3px;
  padding: 13px;
  padding-top: 25px;
  margin: 0;
  margin-bottom: 35px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  margin-bottom: 31px;
  color: $color-default-white;
  cursor: pointer;

  @media screen and (max-width: 1023px) {
    width: 670px;
    padding-top: 27px;
    margin-bottom: 34px;
  }

  @media screen and (max-width: 767px) {
    width: 270px;
    margin-bottom: 30px;
    padding-top: 24px;
    min-height: 180px;
  }
}

.form__textarea:hover {
  background-color: $color-default-white;
}

.form__textarea--modal {
  max-width: 420px;
  margin-bottom: 25px;
}


.form__button {
  box-sizing: border-box;
  width: 265px;
  padding: 14px 35px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
  text-transform: uppercase;
  color: $color-default-white;
  background-color: $color-black-pearl;
  border: none;
  border-radius: 3px;
  margin: 0 43px 10px 0;
  cursor: pointer;

  @media screen and (max-width: 1023px) {
    width: 265px;
    margin-right: 78px;
    letter-spacing: 1px;

  }

  @media screen and (max-width: 767px) {
    width: 270px;
    margin-bottom: 22px;
    margin-right: 0;
  }
}

.form__button:hover {
  background-color: $color-default-white;
  color: $color-black-pearl;
}

.form__button--modal {
  width: 420px;
  padding: 15px 60px;
  margin-bottom: 10px;
}

.form__button-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;

  @media screen and (max-width: 767px) {
    flex-direction: column;
    width: 270px;
  }
}

.form__text--checkbox {
  position: relative;
  display: block;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  margin: 0;
  cursor: pointer;
  width: 231px;

  @media screen and (max-width: 1023px) {
    margin-bottom: 2px;
  }
}

.form__label--checkbox {
  position: relative;
  display: block;
  padding-left: 29px;
  cursor: pointer;
}

.form__label--modal {
  position: relative;
  display: block;
  padding-left: 34px;
}

.form__mark {
  position: absolute;
  top: 10px;
  left: 2px;
  width: 15px;
  height: 15px;
  border: solid 1px $color-default-white;
  border-radius: 2px;

  @media screen and (max-width: 1023px) {
    top: 5px;
    left: 5px;
  }

  @media screen and (max-width: 767px) {
    top: 8px;
  }
}

.form__mark--modal {
  top: 5px;
  left: 7px;
}

.form__checkbox[type="checkbox"]:checked+.form__mark::after,
.form__checkbox--modal[type="checkbox"]:checked+.form__mark--modal::after {
  position: absolute;
  top: 3px;
  left: 1px;
  width: 22px;
  height: 16px;
  background-image: url("../../img/tick.svg");
  background-repeat: no-repeat;
  content: "";
}

.form__label--checkbox:hover .form__text--checkbox {
  color: $color-black-pearl;
}
